import React, { Component } from 'react';
import { Nav, NavItem } from 'reactstrap';
import { NavLink } from 'react-router-dom';

export default class Navbar extends Component {
    render() {
        return (
            <div className="navbar col-sm-12">
                <Nav className="navlinks">
                    <NavLink to="/home" className="navlink" activeClassName="navlink-active">
                        <NavItem><i className="fas fa-home d-lg-none d-xl-none"></i></NavItem>
                        <NavItem className="d-none d-lg-block">HOME</NavItem>
                    </NavLink>
                    <NavLink to="/profile" className="navlink" activeClassName="navlink-active">
                        <NavItem><i className="fas fa-user d-lg-none d-xl-none"></i></NavItem>
                        <NavItem className="d-none d-lg-block">PROFILE</NavItem>
                    </NavLink>
                    <NavLink to="/resume" className="navlink" activeClassName="navlink-active">
                        <NavItem><i className="fas fa-file-alt d-lg-none d-xl-none"></i></NavItem>
                        <NavItem className="d-none d-lg-block">RESUME</NavItem>
                    </NavLink>
                    <NavLink to="/portfolio" className="navlink" activeClassName="navlink-active">
                        <NavItem><i className="fas fa-briefcase d-lg-none d-xl-none"></i></NavItem>
                        <NavItem className="d-none d-lg-block">PORTFOLIO</NavItem>
                    </NavLink>
                    <NavLink to="/contact" className="navlink" activeClassName="navlink-active">
                        <NavItem><i className="fas fa-map-marker-alt d-lg-none d-xl-none"></i></NavItem>
                        <NavItem className="d-none d-lg-block">CONTACT</NavItem>
                    </NavLink>
                </Nav>
            </div>
        )
    }
}
