import React, { Component, Fragment } from 'react';
import { Container, Row } from 'reactstrap';
import LeftSide from '../../organisms/LeftSide';
import Navbar from '../../organisms/Navbar';
import MyData from '../../../components/atoms/profile/MyData';

class Profile extends Component {
    constructor(props){
        super(props)
        this.state={
            effect: false
        }
    }
    componentDidMount(){
        this.setState({
            effect: true
        })
    }
    render() {
        return (
            <Fragment>
                <div className="main-wrapper">
                    <Container>
                        <div className="wrapper card col-sm-12 shadow-lg">
                            <Row>
                                <LeftSide/>
                                <div className="right-side col-sm-8">
                                    <div className="row">
                                        <Navbar/>
                                    </div>
                                    <div className="row">
                                        <div className="main-container text-info bg-white">
                                            <div className="container">
                                                <div className={this.state.effect ? "effect profile-wrap col-sm-12 mx-auto" : "profile-wrap col-sm-12 mx-auto"}>
                                                    <MyData />
                                                    <div className="my-education mt-5">
                                                        <h5 className="text-center">MY <b>EDUCATION</b></h5>
                                                        <hr className="bold" />
                                                    </div>
                                                    <div className="my-hobby"></div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Row>
                        </div>
                    </Container>
                </div>
            </Fragment>
        )
    }
}

export default Profile;
