import React, { Component, Fragment } from 'react';
import { Container, Row } from 'reactstrap';
import LeftSide from '../../organisms/LeftSide';
import Navbar from '../../organisms/Navbar';

class Resume extends Component {
    render() {
        return (
            <Fragment>
                <div className="main-wrapper">
                    <Container>
                        <div className="wrapper card col-sm-12 shadow-lg">
                            <Row>
                                <LeftSide/>
                                <div className="right-side col-sm-8">
                                    <div className="row">
                                        <Navbar/>
                                    </div>
                                    <div className="row">
                                        <div className="main-container text-info bg-white">
                                            <div className="container">
                                                <div className="home-wrap col-sm-11 mx-auto">
                                                    <h2>Resume Page</h2>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Row>
                        </div>
                    </Container>
                </div>
            </Fragment>
        )
    }
}

export default Resume;
